body {
  font-family: "IBM Plex Sans", sans-serif;
}

table {
  margin: 0 auto;
  min-width: 75%;
}

th {
  background-color: #e0e0e0;
  color: #161616;
  padding: 20px;
  font-weight: 500;
  font-size: 14px;
  text-align: left;
}

td {
  color: #525252;
  border-bottom: 1px solid #e0e0e0;
  padding: 20px;
  font-weight: 200;
  font-size: 13px;
}

tr.striped td {
  background-color: #dcdcdc;
}

.material-symbols-outlined {
  cursor: pointer;
}